












































































































































// @ts-nocheck
import {onQROK} from "@/common/common";
import PreNext from './PreNext.vue'
import {sleep} from "@/common/common";
import bottom from './bottom.vue'

export default  {
    name: "GenerateQR",
    data() {
        return {
            QR: null,
            active: 0,
            steps: ["文字", "图片", "Tip", "下载"],
            inputContent: "",
            imgData: null,
            tip: "这是tip文字,您可以修改的",
            icon: "💖",
            realimg: null,
        };
    },
    components: {
        PreNext,
        bottom,
    },
    computed: {
        tipResolve() {
            var tip = this.tip;
            if (!tip) {
                return tip;
            }
            tip = tip.replace(/[\r\n]/g, "<br>")
            return tip;
        }
    },
    created() {
    },
    mounted() {
        onQROK().then(QR => {
            this.QR = QR;

            this.render()
        })
    },
    watch: {
        inputContent(newVal) {
            this.render();
        },
        imgData(newVal) {
            this.render();
        }
    },
    methods: {
        buildImgElementFromCanvas(canvas) {
            return new Promise((resolve, reject) => {
                let img = document.createElement("img");
                img.src = canvas.toDataURL();
                img.onload = () => {
                    resolve(img);
                }
                img.onerror = () => {
                    resolve(null)
                }
            })
        },
        download() {
            let html2canvas = window['html2canvas'] as any;
            let containerWrap = document.getElementById("final-container-wrap")
            html2canvas(containerWrap, {
                backgroundColor: null,
                allowTaint: true,
                logging: true,
                removeContainer: true,
                width: containerWrap.getBoundingClientRect().width + 20,
                height: containerWrap.getBoundingClientRect().height + 20,
            }).then((canvas) => {
                this.buildImgElementFromCanvas(canvas).then(img => {
                    if (img) {
                        img.width = 200;
                        this.$refs.realimg.innerHTML = "";
                        this.$refs.realimg.appendChild(img);
                        this.realimg = img;
                    }
                });

                var oA = document.createElement("a");
                oA.download = 'awesome-qrcode.png';// 设置下载的文件名，默认是'下载'
                oA.href = canvas.toDataURL();
                document.body.appendChild(oA);
                oA.click();
                oA.remove(); // 下载之后把创建的元素删除


            });
        },
        async render() {
            await sleep(400);
            let qrcodeHeight = this.$refs.qrcode.getBoundingClientRect().width;
            this.$refs.qrcode.style.height = `${qrcodeHeight}px`;

            this.QR.create({
                text: this.inputContent,
                size: 800,
                margin: 20,
                backgroundImage: this.imgData || undefined,
                bindElement: 'qrcode'
            });
        },
        listenFileInput(e) {
            if (!e.target.files[0]) {
                this.imgData = null;
                return;
            }
            var img = new Image();
            img.onload = () => {
                this.imgData = img;
            };
            img.src = URL.createObjectURL(e.target.files[0]);
        },
        prev() {
            this.active --;
        },
        next() {
            this.active ++;
        },
    }
}
